import types from '../types.js';
import axios from 'axios';
var state = {
    goods: [],
    food: {},
    foodshow: false
}

var getters = {
    goods(state) {
        return state.goods;
    },
    food(state) {
        return state.food;
    },
    foodshow(state) {
        return state.foodshow;
    }

}

var actions = {
    getGoods({ commit, state }) {
        axios.get('./data.json').then((res) => {
            // console.log(res.data)
            // if (res.data.errno === 0) {
            //类型 （函数命 方法名） 参数
            commit(types.GET_GOODS, res.data.goods);
            // }
        })
    },
    showFood({ commit, state }, f) {
        commit('cc', f);
    },
    closeDetail({ commit }) {
        commit('closeDetail');
    }
}

var mutations = {

    [types.GET_GOODS](state, data) {
        state.goods = data;
    },
    cc(state, f) {
        state.food = f;
        state.foodshow = true;
    },
    closeDetail(state) {
        state.foodshow = false;
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


