<template>
	<div class="raings">
		<div class="ratings-content">
			<div class="overview">
				<div class="overview-left">
					<h1 class="score">{{seller.score}}</h1>
					<div class="title">综合评分</div>
					<div class="rank">高于周边商家{{seller.rankRate}}%</div>
				</div>
				<div class="overview-right">
					<div class="score-wrapper">
						<span class="title">服务态度</span>
						<star :size="'small'" :score="seller.serviceScore"></star>
						<span class="score">{{seller.serviceScore}}</span>
					</div>
					<div class="score-wrapper">
						<span class="title">商品评分</span>
						<star :size="'small'" :score="seller.foodscore"></star>
						<span class="score">{{seller.foodScore}}</span>
					</div>
					<div class="score-wrapper">
						<span class="title">送达时间</span>
						<span class="delivery">{{seller.deliveryTime}}分钟</span>
					</div>
				</div>
			</div>
			<div class="split"></div>
			<ratingSelect></ratingSelect>
			<div class="rating-wrapper">
				<ul>
					<li v-for='(rating,i) in selectRatings' class="rating-item" :key="i">
						<div class="avatar">
							<img :src="rating.avatar" alt="" width="28" height="28">
						</div>
						<div class="content">
						<h1 class="name">{{rating.username}}</h1>
						<div class="star-wrapper">
							<star :size="'small'" :score="rating.score"></star>
							<span class="delivery" v-show="rating.deliveryTime">
								{{rating.deliveryTime}}分钟送到
							</span>
						</div>
						<p class="text">{{rating.text}}</p>
						<div class="recommend" v-show="rating.recommend &&rating.recommend.length">
							<i class="iconfont icon-damuzhion"></i>
							<span  class="item" v-for="(item,i) in rating.recommend" :key="i">{{item}}</span>
						</div>
						<div class="time">
							{{rating.rateTime | formatDate}}
						</div>
					</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	import star from '../star/star.vue';
	import BScroll from 'better-scroll';
	import ratingSelect from '../ratingSelect/ratingSelect.vue';
	import { mapGetters } from 'vuex';
	// import {formatDate} from '../../../static/js/date';
	
	const ALL = 2	
	export default {
		 components:{
			 star,
			 ratingSelect,
			
		},
		 data(){
        return {
		
        }
    },
  		computed:{
      		...mapGetters([
			  	'seller',
			  	'ratings',
				'selectRatings'
      		])
		  },
		  created(){
			this.$store.dispatch('getRatings');
			this.$nextTick(()=>{
            this.scroll = new BScroll(this.$el,{click:true});
        });
		},
		
			filters: {
			formatDate(obj) {
 			let date =  new Date(obj);
			let y = 1900+date.getYear();
			let m = "0"+(date.getMonth()+1);
			let d = "0"+date.getDate();
			return y+"-"+m.substring(m.length-2,m.length)+"-"+d.substring(d.length-2,d.length);  
		}
  		 },
		
	}

</script>

<style scoped lang="less">
	.raings{
		position: absolute;
		top: 3.5rem;
		bottom: 0px;
		left: 0px;
		width: 100%;
		overflow: hidden;
		.overview{
			display: flex;
			padding: 0.36rem 0 0.36rem 0.36rem;
			.overview-left{
				flex: 0 0 2.75rem;
				padding: 0.12rem 0;
				border-right: 1px solid rgb(147, 153, 159);
				text-align: center;
				@media only screen and (max-width:3.20rem){
					flex: 0 0 1.1rem;
					width: 1.1rem;
				}
				.score{
					margin-bottom: 0.12rem;
					line-height: 0.56rem;
					font-size: 0.48rem;
					color: rgb(255, 153,0);
				}
				.title{
					margin-bottom: 0.16rem;
					line-height: 0.24rem;
					font-size: 0.24rem;
					color: rgb(7,17, 27);
				}
				.rank{
					line-height: 0.2rem;
					font-size: 0.24rem;
					color: rgb(147, 153, 159);
				}
			}
			.overview-right{
				flex: 1;
				padding: 0.06rem 0 0.06rem 0.48rem;
				.score-wrapper{
					line-height: 0.16rem;
					margin-top: 0.16rem;
					font-size: 0.24rem;
					.title{
						display: inline-block;
						vertical-align: top;
						line-height: 0.36rem;
						font-size: 0.24rem;
						color: rgb(7, 17, 27);
					}
					.star{
						display: inline-block;
						margin: 0 0.12rem;
						line-height: 0rem;
					}
					.score{
						display: inline-block;
						vertical-align: top;
						line-height: 0.36rem;
						font-size: 0.24rem;
						color: rgb(255, 153, 0);
					}
					.delivery{
						display:inline-block;
						margin-left:0.24rem;
						vertical-align:top;
						line-height:0.36rem;
						font-size:0.24rem;
						color:rgb(147, 153, 159);
					}
				}
			}
		}
		.split{
			width: 100%;
			height: 0.32rem;
			border: 1px solid #f3f5f7;
			background: #f3f5f7;
			border-top: 1px solid  #e7e8ea;
			border-bottom: 1px solid  #e7e8ea;
		}
		.rating-wrapper{
			.rating-item{
				display: flex;
				padding :0.36rem;
				border:1px(rgba(1, 17, 27, 0.1));
				border-bottom: 1px solid #e8e8e8;
				.avatar{
					flex: 0 0 28px;
					width: 28px;
					margin-right: 12px;
					img{
						border-radius: 50%;
					}
				}	
				.content{
					position :relative;
					flex :1;
					.name{
						margin-bottom: 4px;
						line-height :12px;
						font-weight: 700;
						font-size :10px;
						color: rgb(7, 17, 27);
					}
					.star-wrapper{
						margin-bottom: 0.12rem;
						font-size: 0;
						.star{
							display :inline-block;
							margin-right: 16px;
							// vertical-align: top;
							line-height: 0;
						}
						.delivery{
							display :inline-block;
							vertical-align: top;
							font-size :10px;
							line-height: 12px;
							color :rgb(147, 153, 159);
						}
					}	
					.text{
						line-height: 18px;
						color :rgb(7, 17, 27);
						font-size: 0.24rem;
						margin-bottom: 8px;
					}
					.recommend{
						line-height :16px;
						font-size :0;
						.iconfont, .item{
							display: inline-block;
							margin :0 8px 4px 0;
							font-size: 9px;
						}
						.iconfont{
							color :rgb(0, 160, 220);
						}
						.item{
							padding: 0 6px;
							border :1px solid rgba(7, 17, 27, 0.1);
							border-radius: 1px;
							color :rgb(147, 153, 159);
							background: #ffffff;
						}
					}
					.time{
						position :absolute;
						top: 0;
						right: 0;
						line-height: 12px;
						font-size :0.24rem;
						color: rgb(147, 153, 159);
					}
				}
			}
		}
	}
</style>


