<template>
  <div class="seller">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc border-1px">
          <star :size="'small'" :score="seller.score"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
		<div class="lineone"></div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}</span>分钟
            </div>
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorite($event)">
          <!-- <i class="icon iconfont icon-aixin" :class="{'active':favorite}"></i> -->
          <span>{{favoriteText}}</span>
        </div>
      </div>
      <div class="split"></div>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper ">
          <p class="content">{{seller.bulletin}}</p>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item" v-for="(item, index) in seller.supports">
            
						<div class="linetwo"> </div>
            <span class="icon" :class="classMap[seller.supports[index].type]"></span>
            <span class="text">{{seller.supports[index].description}}</span>
						<!-- <div class="linetwo"> </div> -->
          </li>
        </ul>
      </div>
			<div class="split"></div>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" ref="picWrapper">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="pic in seller.pics">
              <img :src="pic" width="120" height="120">
            </li>
          </ul>
        </div>
      </div>
			<div class="split"></div>
      <div class="info">
        <div class="title  border-1px">商家信息</div>
				<div class="linethree"></div>
        <ul>
          <li class="info-item" v-for="info in seller.infos">
							<span class="text">	{{info}}</span>
							<div class="linefour"></div>
					</li>
        </ul>
      </div>
      
    </div>
  </div>
</template>

<script>
import star from "../star/star.vue";
import BScroll from "better-scroll";
import { mapGetters } from "vuex";
export default {
  components: {
  
    star
  },
  computed: {
    ...mapGetters(["seller"]),
    favoriteText() {
      return this.favorite ? "已收藏" : "收藏";
    }
  },
  created() {
    if (!this.picScroll) {
      if (this.seller.pics) {
        this.$nextTick(() => {
          let picWidth = 120;
          let margin = 6;
          let width = (picWidth + margin) * this.seller.pics.length - margin;
          this.$refs.picList.style.width = width + "px";
          this.picScroll = new BScroll(this.$refs.picWrapper, {
            scrollX: true,
            eventPassthrough: "vertical"
          });
        });
      }
    } else {
      this.picScroll.refresh();
    }
    if (!this.scroll) {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$el, { click: true });
      });
    } else {
      this.scroll.refresh();
    }
    this.classMap = ["decrease", "discount", "special", "invoice", "guarantee"];
  },
  methods: {
    _initScroll() {},
    toggleFavorite(event) {
      if (!event._constructed) {
        return;
      }
      this.favorite = !this.favorite;
      savaToLocal(this.seller.id, "favorite", this.favorite);
    }
  }
};
</script>

<style scoped lang="less">
.seller {
  position: absolute;
  top: 3.7rem;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  .overview {
    padding: 0.36rem;
    .title {
      margin-bottom: 8px;
      line-height: 14px;
      color: rgb(7, 17, 27);
      font-size: 0.28rem;
    }
    .desc {
      // padding-bottom :18px;
      font-size: 0.2rem;
      color: rgb(7, 17, 27);
      line-height: 0.36rem;
    }
    .star {
      display: inline-block;
      vertical-align: top;
      margin-right: 8px;
      line-height: 0;
    }
    .text {
      display: inline-block;
      vertical-align: top;
      margin-right: 12px;
      line-height: 0.2rem;
      font-size: 0.2rem;
      color: rgb(77, 85, 93);
    }
  }
  .lineone {
    border: 1px solid #e8e8e8;
    width: 100%;
    margin-top: 0.36rem;
    margin-bottom: 0.18rem;
  }
  .remark {
    display: flex;
    padding-top: 18px;
  }
  .block {
    flex: 1;
    text-align: center;
    border-right: 1px solid rgba(7, 17, 27, 0.1);
    &:last-child {
      border: none;
    }
    h2 {
      margin-bottom: 4px;
      line-height: 10px;
      font-size: 10px;
      color: rgb(147, 153, 149);
    }
    .content {
      line-height: 0.48rem;
      font-size: 48px/20px;
      font-weight: 200;
      color: rgb(7, 17, 27);
      margin-top: 0.08rem;
    }
    .stress {
      font-size: 24px;
    }
  }
  .favorite {
    position: absolute;
    right: 11px;
    top: 18px;
    width: 50px;
    text-align: center;
    .iconfont {
      display: block;
      margin-bottom: 4px;
      line-height: 24px;
      font-size: 24px;
      width: 50px;
      color: #d4d6d9;
    }
    &.active {
      color: rgb(240, 20, 20);
    }
    .text {
      line-height: 10px;
      font-size: 10px;
      color: rgb(77, 85, 93);
    }
  }
  .split {
    width: 100%;
    height: 0.32rem;
    border: 1px solid #f3f5f7;
    background: #f3f5f7;
    border-top: 1px solid #e7e8ea;
    border-bottom: 1px solid #e7e8ea;
  }
  .bulletin {
    padding: 0.36rem 0.24rem 0 0.36rem;
    .title {
      margin-bottom: 8px;
      line-height: 14px;
      color: rgb(7, 17, 27);
      font-size: 14px;
    }
    .content-wrapper {
      padding: 0 12px 16px 1px;
      .content {
        line-height: 0.48rem;
        font-size: 0.24rem;
        color: rgb(240, 20, 20);
        margin-left: 0.24rem;
      }
    }
    .support-item {
      line-height: 0.96rem;
      font-size: 0.24rem;
      height: 0.96rem;
      .icon {
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: top;
        background-size: 16px 16px;
        margin-top: 15px;
        margin-left: 0.2rem;
				background-repeat: no-repeat;
				 &.decrease{
						background-image: url('../../../static/imgs/decrease.png');
						}
          &.discount{
						background-image: url('../../../static/imgs/discount.png');
						}
          &.guarantee{
						background-image: url('../../../static/imgs/guarantee.png');
						}
          &.invoice{
						background-image: url('../../../static/imgs/invoice.png');
						}
          &.special{
						background-image: url('../../../static/imgs/special.png');
						}
      }
      .text {
        display: inline-block;
        font-size: 0.24rem;
        // margin-left: 0.24rem;
        font-weight: 200;
        color: rgb(7, 17, 27);
      }
      .linetwo {
        border: 1px solid #e8e8e8;
        width: 100%;
      }
    }
  }
  .pics {
    padding: 18px;
    .title {
      margin-bottom: 12px;
      line-height: 14px;
      color: rgb(7, 17, 27);
      font-size: 14px;
    }
    .pic-wrapper {
      width: 100%;
      overflow: hidden;
      white-space: nowrap; /*不这行*/
      .pic-list {
        font-size: 0;
        .pic-item {
          display: inline-block;
          margin-right: 0.12rem;
          &:last-child {
            margin: 0;
          }
        }
      }
    }
  }
  .info {
    padding: 18px 18px 0 18px;
    color: rgb(7, 17, 27);
    .title {
      padding-bottom: 12px;
      line-height: 14px;
      border: -1px(rgba(7, 17, 27, 0.1));
      font-size: 14px;
    }
    .linethree {
      border: 1px solid #e8e8e8;
      width: 100%;
    }
    .info-item {
      padding: 0.32rem 0 0.16rem 0;
      line-height: 0.32rem;
      border: 1px(rgba(7, 17, 27, 0.1));
      padding-bottom: 0;
      .text {
        margin-left: 0.24rem;
        font-size: 0.24rem;
        font-weight: 200;
      }
      .linefour {
        border-bottom: 1px solid #e8e8e8;
        margin-top: 0.32rem;
      }
    }
  }
}
</style>


