<template>
	<div class="select">
		<div class="type">
			<div class="one demo" @click='select(2)' >
               <span>{{types[2]}}{{ratings.length}} </span> 
            </div>
			<div class="one demk" @click='select(0)'>
               <span>{{types[0]}}{{goodCount}}</span> 
            </div>
			<div class="one demi" @click='select(1)'>
                <span>{{types[1]}}{{badCount}}</span> 
            </div>
		</div>
        <div class="lineone"></div>
		<div class="toggle" @click = 'toggle'>
            <i class="icon iconfont icon-duihao"></i>
			只看有内容的评价
		</div>
        <div class="linetwo"></div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex';
	export default {
		computed:{
			...mapGetters([
				'types',
				'ratings',
				'selectRatings'
			]),
			goodCount(){
				return this.ratings.filter( rating => {
					return rating.rateType === 0; 
				}).length;
			},
			badCount(){
				return this.ratings.filter( rating => {
					return rating.rateType === 1; 
				}).length;
			}
		},
		// 初始化页面
		created(){
			this.$store.dispatch('changeTypes',['推荐','吐槽','全部']);
		},
		methods:{
			select(c){
				this.$store.dispatch('select',c)
			},
			toggle(){
				this.$store.dispatch('toggle');
			}
		}

	}
</script>

<style lang="less" scoped>
	.select{
	
		.type{
            // padding :18px 0;
            // margin:0 18px;
            padding: 0.36rem 0 0.36rem 0.36rem;
			.one{
                width: 1.2rem;
                height: 0.65rem;
                // padding :8px 12px;
                margin-right :0.1rem;
                border-radius :5px;
                font-size :0.24rem;
				border: 0px solid #000;
                line-height: 0.65rem;
                display:inline-block;
                text-align: center;
            }
            .demo{
                background-color:#00a0dc;
            }
            .demk{
                background-color: #ccecf7;
            }
            .demi{
                background-color: #eaebed;
            }
        }
        .lineone{
				border:1px solid #e8e8e8;
				width:90%;
                margin-left: 0.36rem;
			}
		.toggle{
			width: 3rem;
            padding: 0.36rem 0 0.36rem 0.36rem;
            color: rgb(147, 153, 159);
            font-size: 0.24rem;
        }
        .linetwo{
            border:1px solid #e8e8e8;
			width:100%;
        }
	}
</style>


