<template>
	<div class="cart">
		<div class="reduce" v-show="food.count" @click.stop.prevent="reduce($event)" >
			<i class="iconfont icon-jianhao"></i>
		</div>
		<div class="count" v-show="food.count">{{food.count}}</div>
		<div class="add" @click.stop.prevent = 'add'>
			<i class="iconfont icon-jiahao"></i>			
		</div>
	</div>
</template>

<script>
	import Vue from 'vue';
	export default {
		props:['food'],
		methods:{
			add(){
				if( !this.food.count ){
					Vue.set( this.food , 'count' , 1);
				}else{
					this.food.count ++;
				}
				this.$emit('increment', event.target); 
			},
			reduce(){
				if( this.food.count ){
					this.food.count --;
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	.cart{
		width: 1.2rem;
		height: 0.35rem;
		// border: 1px solid #000;
		display: flex;
		position: relative;
		bottom: -0.01rem;
		right: 0.8rem;
		.reduce{
			flex:1;
			// border: 1px solid #000;
			display: inline-block;
			vertical-align: top;
			color: rgb(0, 160, 220, 0.2);
			position: absolute;
			// right: 1.8rem;
			left: 0.5rem;
		}
		.count{
			flex:1;
			// border: 1px solid #000;
			display: inline-block;
			vertical-align: top;
			text-align: center;
			color: rgb(147, 153, 159);
			position: absolute;
			// right: 1.5rem;
			left: 0.9rem;
			top: 0.05rem;
		}
		.add{
			flex:1;
			// border: 1px solid #000;
			display: inline-block;
			vertical-align: top;
			color: rgb(0, 160, 220, 0.2);
			position: absolute;
			// right: 1rem;
			left: 1.2rem;
		}
	}

</style>


