<template>
	<div class="food" v-show='foodshow' ref="food">
		<div>
			<div class="imgshow" @click="closeDetail" >
				<img :src="food.image" alt="" >
				<div class="content">
                	<h2 class="name">{{food.name}}</h2>
                	<div class="extra">
                  		<span class="count">月售{{food.sellCount}}份</span>
                  		<span class="count">好评{{food.rating}}%</span>
                	</div>

                	<div class="price">
                  		<span class="now">￥{{food.price}}</span>
                  		<span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                	</div>  

					<div class="shopcart">
						<p class="jion" v-show='!food.count' @click = 'addFirst'>加入购物车</p>
						<cartControl :food = 'food' v-show='food.count'></cartControl>
					</div>   
					   
            	</div>
			</div>
			<div class="fenge"></div>
			<div class="introduce"  v-show="food.info">
				<div class="int">商品介绍</div>
				<div class="info">{{food.info}}</div>
			</div>
			<div class="fenge1"></div>
			<div class="assess">
				<div class="title">商品评价</div>

				<ratingSelect   @increment="incrementTotal" :select-type="selectType" :only-content="onlyContent" :desc="desc"
                        :ratings="food.ratings" ></ratingSelect>

				<div class="rating-wrapper">
					<ul v-show="food.ratings && food.ratings.length">
              			<li v-show="needShow(rating.rateType, rating.text)" class="rating-item border-1px"
                  		v-for="(rating,i) in food.ratings" :key="i">
                			<div class="user">
                  				<span class="name">{{rating.username}}</span>
                  				<img width="12" height="12" :src=rating.avatar alt="" class="avatar">
                			</div>
                			<div class="time">{{rating.rateTime | formatDate}}</div>
                			<p class="text">
                  			<i class="iconfont"
								:class="{'icon-damuzhi':rating.rateType === 0,'icon-down':rating.rateType === 1,}"></i>
							{{rating.text}}
                			</p>
              			</li>
            		</ul>
            		<div class="no-rating" v-show="!food.ratings || food.ratings.length === 0"></div>
         		</div>
			</div>
		</div>
	</div>	
</template>

<script>
	import BScroll from "better-scroll";
	import { mapGetters } from 'vuex';
	import Vue from 'vue';
	import cartControl from '../cartControl/cartControl.vue';
	import ratingSelect from '../ratingSelect/ratingSelect.vue';
	import star from "../star/star.vue";
	// import {formatDate} from '../../../static/js/date';
const ALL = 2;
	export default {
		created(){
			this.$store.dispatch('getRatings');
        },
		computed:{
			...mapGetters([
				'foodshow',
				'food',
				'seller',
				'selectRatings',
				'ratings',
				'aa'
			])
		},
		components:{
			cartControl,
			star,
            ratingSelect
		},
		watch: {
			food(){
				this.$nextTick( () => {
					if( !this.foodScroll ){
						this.foodScroll = new BScroll(this.$refs.food ,{
							click : true
		 				})
					}else{
						this.foodScroll.refresh();
					}
					// this.cc();
				})
			}
		},
			data () {
				return {
					showFlag: false,
					selectType: ALL,
					onlyContent: true,
					desc: {
					all: '全部',
					positive: '推荐',
					negative: '吐槽'
					}
				};
			},
		methods: {
			show() {
				this.showFlag = true;
				this.selectType = ALL;
				this.onlyContent = true;
				this.$nextTick(() => {
				if (!this.scroll) {
					this.scroll = new BScroll(this.$el, {
					click: true
					});
				} else {
					this.scroll.refresh();
				}
				});
			},
			incrementTotal(type, data) {
				this[type] = data;
				this.$nextTick(() => {
				this.scroll.refresh();
				});
			},
			menuBS() {
				let menuBScroll = new BScroll(this.$refs.food, {
					click:true
				})
			},
			closeDetail(){
				this.$store.dispatch('closeDetail');
			},
			addFirst(){

				if( !this.food.count )
				{
					Vue.set( this.food , 'count' , 1 );
				}
			},
			 needShow(type, text) {
				if (this.onlyContent && !text) {
				return false;
				}
				if (this.selectType === ALL) {
				return true;
				} else {
				return type === this.selectType;
				}
			}
				},
		filters:{
		formatDate(obj) {
 			let date =  new Date(obj);
			let y = 1900+date.getYear();
			let m = "0"+(date.getMonth()+1);
			let d = "0"+date.getDate();
			return y+"-"+m.substring(m.length-2,m.length)+"-"+d.substring(d.length-2,d.length);  
		}
	}
	}
</script>

<style lang="less" scoped>
	.food {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: white;
		.imgshow{
			position: relative;
			width: 100%;
			height: 0;
			padding-top: 100%;
			img{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
			 .content{
                flex :1;
          		.name{
            		padding :0.36rem 0 0.16rem 0.36rem;
					font-size: 0.28rem;
					font-weight: 700;
            		line-height: 0.28rem;
            		height :0.28rem;
            		color: rgb(7, 17, 27);
         		}            
          		.desc, .extra{
					padding-left: 0.36rem;
            		font-size :0.2rem;
            		line-height: 0.2rem;
            		color: rgb(147, 153, 159);
          			}
          		.desc{
					padding:0.16rem 0 0.24rem 0.36rem;
           			margin-top: 0.16rem;
            		line-height: 0.24rem;
          			}            
          		.extra{
            		.count{
              			margin-right: 0.24rem;
            		}    
          		}            
          		.price{
					margin-top: 0.36rem;
					padding-left: 0.36rem;
            		font-weight: 700;
            		line-height: 0.24rem;
            		.now{
              			margin-right: 0.16rem;
              			font-size: 0.28rem;
              			color: rgb(240, 20, 20);
            		}
            		.old{
              			font-size :0.2rem;
              			color: rgb(147, 153, 159);
              			text-decoration: line-through;
            		}
				}
				.shopcart{
					position: absolute;
					top: 8.65rem;
					left: 5.65rem;
					.jion{
						width: 1.48rem;
						height: 0.36rem;
						border-radius: 0.22rem;
						background:#00a0dc;
						text-align: center;
						font-size: .2rem;
						color: rgb(255,255,255);
						line-height: 0.36rem;
						padding: 0.12rem 0;
					}
				} 
			}
		}
		.fenge{
			width: 100%;
			height: 0.32rem;
			background: #f3f5f7;
			border-top: 1px solid rgba(7,17,27,0.1);
			border-bottom: 1px solid rgba(7,17,27,0.1);
			margin-top:2.05rem;
		}
		.introduce{
			width: 100%;
			.int{
				font-size: 0.28rem;
				padding: 0.36rem 0 0.12rem 0.36rem;
			}
			.info{
				padding: 0.12rem 0.52rem 0.36rem 0.52rem;
				font-size: 0.24rem;
				font-weight: 200;
				color: rgb(77,85,93);
				line-height: 0.48rem;
			}
		}
		.fenge1{
			width: 100%;
			height: 0.32rem;
			background: #f3f5f7;
			border-top: 1px solid rgba(7,17,27,0.1);
			border-bottom: 1px solid rgba(7,17,27,0.1);
		}
		.assess{
			padding-left:0.36rem ;
			.title{
				font-size: 0.28rem;
				padding: 0.36rem 0 0.12rem 0;
			}
			.rating-wrapper{
				padding:0 18px;
				.rating-item{
					position:relative;
					padding :16px 0;
					border:-1px(rgba(1, 17, 27, 0.1));
					.user{
						position: absolute;
						right: 0;
						top: 16px;
						font-size :0;
						line-height: 12px;
					}
					.name{
						display :inline-block;
						vertical-align: top;
						font-size :10px;
						color: rgb(147, 153, 159);
						margin-right: 6px;
					}
					.avatar{
						border-radius: 50%;
					}
					.time{
						margin-bottom: 6px;
						line-height :12px;
						font-size :10px;
						color :rgb(147, 153, 159);
					}
					.text{
						line-height: 16px;
						font-size: 12px;
						color: rgb(7, 17, 27);
					}
					.iconfont{
						margin-right: 4px;
						line-height: 16px;
						font-size: 12px;
					}
					.icon-damuzhi{
						color: rgb(0, 160, 220);
					}
					.icon-down{
						color: rgb(147, 153, 159);
					}
				}		

				.no-rating{
					padding :16px 0;
					font-size: 12px;
					color: rgb(147, 153, 159);
				}
			}		
		}
	}
</style>


