<template>
	<div class="star" :class='starType'>
		<!-- {{starType}} -->
		<br>
		<span v-for='itemClass in itemClasss' v-bind:key='itemClass.id' :class='itemClass' class="star-item"></span>
	</div>
</template>

<script>
const LENGTH = 5;
const ON = "on"; //全的星星
const HALF = "half"; //一半星星
const OFF = "off";

export default {
  props: ["score", "size"],
  computed: {
    starType() {
      return "star-" + this.size;
    },
    itemClasss() {
      // 4.6
      let result = [];
      let integer = Math.floor(this.score); // 4 整数
      let decimal = this.score - integer; // 0.6 小数
      for (let i = 0; i < integer; i++) {
        result.push(ON);
        //['on','on','on','on']
      }
      if (decimal > 0.5) {
        result.push(HALF);
        //['on','on','on','on','half']
      }
      while (result.length < LENGTH) {
        result.push(OFF);
        //['on','on','on','on','half']
      }
      return result;
    }
  }
};
</script>

<style lang="less" scoped>
.star {
  font-size: 0.4rem;

  .star-item {
    display: inline-block;
    width: 0.48rem;
    height: 0.48rem;
    margin-right: 0.1rem;
    &.on {
      background-image: url(./star_on.png);
      background-size: 0.48rem 0.48rem;
      background-repeat: no-repeat;
    }
    &.half {
      background-image: url(./star_half.png);
      background-size: 0.48rem 0.48rem;
      background-repeat: no-repeat;
    }
    &.off {
      background-image: url(./star_off.png);
      background-size: 0.48rem 0.48rem;
      background-repeat: no-repeat;
    }
  }
  &.star-big {
    .star-item {
      display: inline-block;
      width: 0.6rem;
      height: 0.6rem;
      margin-right: 0.2rem;
      &.on {
        background-image: url(./star_on.png);
        background-size: 0.6rem 0.6rem;
        background-repeat: no-repeat;
      }
      &.half {
        background-image: url(./star_half.png);
        background-size: 0.6rem 0.6rem;
        background-repeat: no-repeat;
      }
      &.off {
        background-image: url(./star_off.png);
        background-size: 0.6rem 0.6rem;
        background-repeat: no-repeat;
      }
    }
  }
  &.star-small {
    .star-item {
      display: inline-block;
      width: 0.3rem;
      height: 0.3rem;
      margin-right: 0.1rem;
      &.on {
        background-image: url(./star_on.png);
        background-size: 0.3rem 0.3rem;
        background-repeat: no-repeat;
      }
      &.half {
        background-image: url(./star_half.png);
        background-size: 0.3rem 0.3rem;
        background-repeat: no-repeat;
      }
      &.off {
        background-image: url(./star_off.png);
        background-size: 0.3rem 0.3rem;
        background-repeat: no-repeat;
      }
    }
  }
}
</style>


