<template>
    <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">
        <div class="detail" v-show = 'detailShow'>
            <div class="bg"></div>
            <p class="seller-name" >{{seller.name}}</p>
            <div>
                <star :score='seller.score' :size = "'big'" class="star"></star>
            </div>
            <div class="line-left"></div>
            <div class="line-right"></div>
            <div class="info">
                <p>优惠信息</p>
            </div>
            <div class="detail-info">
                <li>
                    <img src="../../../static/imgs/decrease.png" alt="">
                    <p v-if="seller.supports">{{seller.supports[0].description}}</p>
                </li>
                <li>
                    <img src="../../../static/imgs/discount.png" alt="">
                    <p v-if="seller.supports">{{seller.supports[1].description}}</p>
                </li>
                <li>
                    <img src="../../../static/imgs/discount.png" alt="">
                    <p v-if="seller.supports">{{seller.supports[2].description}}</p>
                </li>
                <li>
                    <img src="../../../static/imgs/special.png" alt="">
                    <p v-if="seller.supports">{{seller.supports[3].description}}</p>
                </li>
                <li>
                    <img src="../../../static/imgs/special.png" alt="">    
                    <p v-if="seller.supports">{{seller.supports[4].description}}</p>
                </li>
            </div>
            <div class="bulletin">
                <div class="line-left"></div>
                <div class="line-right"></div>
                <p>商家公告</p>
                <div class="textarea">
                    <p>
                        {{seller.bulletin}}
                    </p>
                </div>
            </div>
            <i class="iconfont icon-X" @click='hideDetail'  ></i>
            
        </div>
    </transition>
</template>

<script>
import { mapGetters } from "vuex";
import animate from "animate.css";
import star from "../star/star.vue";
export default {
  props: ["name"],
  components: {
    star
  },
  computed: {
    ...mapGetters(["seller", "detailShow"])
  },
  methods: {
    hideDetail() {
      this.$store.dispatch("hideDetail");
    }
  }
};
</script>

<style scoped lang="less">
.detail {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    .bg {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(7, 17, 27, 0.8);
        filter: blur(0.01rem);
    }
    .seller-name {
        font-size: 0.32rem;
        font-weight: 700;
        color: white;
        line-height: 0.32rem;
        text-align: center;
        position: relative;
        top: 1.28rem;
        filter: blur(0);
    }
    .star {
        position: relative;
        top: 1.28rem;
        text-align: center;
        filter: blur(0);
    }
    .line-left{
        width: 2.24rem;
        height: 0.32rem;
        border-bottom: 0.04rem solid rgba(255, 255, 255, 0.2);
        position: absolute;
        top: 2.96rem;
        left: 0.72rem;
        filter: blur(0);
    }
    .line-right{
        width: 2.24rem;
        height: 0.32rem;
        border-bottom: 0.04rem solid rgba(255, 255, 255, 0.2);
        position: absolute;
        top: 2.96rem;
        right: 0.72rem;
        filter: blur(0);
    }
    .info{
        p{  
            font-size: 0.32rem;
            font-weight: 700;
            color: white;
            line-height: 0.32rem;
            text-align: center;
            position: relative;
            top: 1.72rem;
        }
    }
    .detail-info{
        li{
            filter: blur(0);
            color: white;
            position: relative;
            top: 2.24rem;
            left: 0.96rem;
            img{
                display: block;
                width:0.32rem;
                height: 0.32rem;
            }
            p{
                font-size: 0.24rem;
                font-weight: 200;
                color: white;
                line-height: 0.24rem;
                position: relative;
                left: 0.44rem;
                top:-0.28rem;
            }
            
        }
    }
    .bulletin{
        filter: blur(0);
        .line-left{
            width: 2.24rem;
            height: 0.32rem;
            border-bottom: 0.04rem solid rgba(255, 255, 255, 0.2);
            position: absolute;
            top: 6.88rem;
            left: 0.72rem;
            filter: blur(0);
        }
        .line-right{
            width: 2.24rem;
            height: 0.32rem;
            border-bottom: 0.04rem solid rgba(255, 255, 255, 0.2);
            position: absolute;
            top: 6.88rem;
            right: 0.72rem;
            filter: blur(0);
        }
        p{
            font-size: 0.28rem;
            font-weight: 700;
            color: white;
            line-height: 0.28rem;
            text-align: center;
            position: relative;
            top: 2.56rem;
        }
        .textarea{
            position: relative;
            top: 0.48rem;
            left: 0.96rem;
            width: 5.58rem;
            p{
                font-size: 0.24rem;
                font-weight: 200;
                color: white;
                line-height: 0.48rem;
                text-align: left;
            } 
        }
    }
    .icon-X{
        filter: blur(0);
        position: absolute;
        left: 3.43rem;
        bottom: 0.64rem;
        font-size: 0.64rem;
        color: rgba(255, 255, 255, 0.5);
    }


}
</style>



