<template>
    <div class="goods">
        <div class="menu-wrap" ref='cc'>
          <ul>
				    <li v-for="(item,index) in goods" @click='selectMenu(index)' class="menu-item" :key="index" >
					    <span class="text">
						    {{item.name}}
					    </span>
				    </li>
			    </ul>
        </div>
        <div class="foods-wrap" ref='food'>
          <ul>
        	<li v-for="(item,index) in goods"  class="food-list food-list-hook" :key="index">
          	<h1 class="title">{{item.name}}</h1>
          <ul>
            <li v-for="(food,index) in item.foods"  class="food-item" :key="index" >
              <div class="icon">
                <img :src="food.icon" alt="" @click = 'showFood(food)'>
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}</span><span class="count">好评{{food.rating}}</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">¥{{food.oldPrice}}</span>
                </div>
              </div>
              <div class="cart">
								<cartControl :food = 'food'></cartControl>
							</div>
            </li>
          	</ul>
        		</li>
      		</ul>
        </div>
      <foodDetail></foodDetail>
		<shopCart :goods = 'goods'></shopCart>
    </div>
	
</template>

<script>
import BScroll from "better-scroll";
import { mapGetters } from "vuex";
import foodDetail from '../foodDetail/foodDetail.vue';
import shopCart from '../shopCart/shopCart.vue';
import cartControl from '../cartControl/cartControl.vue';
export default {
  components: {
   foodDetail,
   shopCart,
   cartControl
  },
  data() {
    return {
      listHeight: [],
      scrollY: 0
    };
  },
  created() {
    this.$store.dispatch("getGoods");
  },
 watch: {
			goods(){
				this.$nextTick( () => {
					if( !this.foodBScroll ){
						this.foodBScroll = new BScroll(this.$refs.food ,{
							click : true
		 				})
					}else{
						this.foodBScroll.refresh();
					}
					// this.cc();
				})
			}
		},
  computed: {
    ...mapGetters(["goods"]),
    // 计算当前索引值
    currentIndex() {
      // 循环listHeight数组
      for (let index = 0; index < this.listHeight.length; index++) {
        let height1 = this.listHeight[index]; // 0
        let height2 = this.listHeight[index + 1]; // 1434
        // 根据滚动的y轴坐标值 与listHeight数组区间进行比较
        if (this.scrollY >= height1 && this.scrollY < height2) {
          return index;
        }
      }
      return 0;
    }
  },
  methods: {
    menuBS() {
      this.menuBScroll = new BScroll(this.$refs.cc, {
        click: true
      });
      this.foodBScroll = new BScroll(this.$refs.food, {
        click: true,
        // 作用：有时候我们需要知道滚动的位置。
        // 为 1 的时候，会非实时（屏幕滑动超过一定时间后）派发scroll 事件；当 probeType
        // 为 2 的时候，会在屏幕滑动的过程中实时的派发 scroll 事件；当 probeType
        // 为 3 的时候，不仅在屏幕滑动的过程中，而且在 momentum 滚动动画运行过程中实时派发 scroll 	事件。如果没有设置该值，其默认值为 0，即不派发 scroll 事件。
        probeType: 3
      });

      // 实时获取y坐标
      this.foodBScroll.on("scroll", pos => {
        // console.log(pos.y);
        // console.log( Math.abs( Math.round(pos.y)));
        this.scrollY = Math.abs(Math.round(pos.y));
      });
    },
    // 计算li的高度 并且push进listHeight数组当中
    calcHeight() {
      // 获取所有li
      let foodList = this.$refs.food.getElementsByClassName("food-list-hook");
      // 初始值为0
      let height = 0;
      this.listHeight.push(height);
      // 循环所有li
      for (let i = 0; i < foodList.length; i++) {
        height += foodList[i].clientHeight;
        this.listHeight.push(height);
      }
      // console.log( this.listHeight );
    },
    // 左侧点击事件
    selectMenu(i) {
      // console.log(i);
      let foodList = this.$refs.food.getElementsByClassName("food-list-hook");
      let el = foodList[i];
      // scrollToElement
      this.foodBScroll.scrollToElement(el, 300);
    },
    showFood(food){
				this.$store.dispatch('showFood',food);
			}
  }
};
</script>

<style scoped lang="less">
.goods {
  position: absolute;
  display: flex;
  overflow: hidden;
  width: 100%;
  top: 3.52rem;
  bottom: 0.9rem;
  // border: 1px solid red;
  .menu-wrap {
    flex: 0 0 1.6rem; //不扩展 不缩放
    width: 1.6rem;
    background: #f3f5f7;
    .menu-item {
      width: 1.12rem;
      height: 1.08rem;
      padding: 0 0.24rem;
      display: table;
      &.current {
        position: absolute;
        z-index: 10;
        margin-top: -1px;
        background: #fff;
        font-weight: 700;
      }
      .text {
        display: table-cell;
        font-size: 0.24rem;
        font-weight: 200;
        line-height: 0.28rem;
        vertical-align: middle;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      }
    }
  }
  .foods-wrap {
    flex: 1;
    .food-list {
      .title {
        width: 100%;
        height: 0.54rem;
        padding-left: 0.36rem;
        line-height: 0.54rem;
        border-left: 2px solid #d9dde1;
        font-size: 0.24rem;
        color: rgb(77, 85, 93);
        background: #f3f5f7;
      }
      .food-item {
        display: flex;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        line-height: 0.79rem;
        margin-left: 0.36rem;
        .icon {
          flex: 0 0 0.57rem;

          margin-top: 0.36rem;
          img {
            width: 1.14rem;
            height: 1.14rem;
          }
        }
        .cart{
          margin-top: 0.5rem;
        }
      }
        .content {
          flex: 1px;
          margin-top: 0.36rem;
          margin-left: 0.2rem;
          .name {
            margin: 0.04rem 0 0.16rem 0rem;
            font-size: 0.28rem;
            color: rgb(7, 17, 27);
            line-height: 0.28rem;
            height: 0.16rem;
          }
          .desc,
          .extra {
            font-size: 0.2rem;
            line-height: 0.2rem;
            color: rgb(147, 153, 159);
            margin-top: 0.16rem;
          }
          .desc {
            margin-top: 0.25rem;
            line-height: 0.32rem;
          }
          .extra {
            .count {
              margin-right: 0.24rem;
            }
          }
          .price {
            font-weight: normal/700;
            line-height: 0.48rem;
            .now {
              margin-right: 0.16rem;
              font-size: 0.2rem;
              color: rgb(240, 20, 20);
            }
            .old {
              font-size: 0.2rem;
              color: rgb(147, 153, 159);
              text-decoration: line-through;
            }
          }
        }
      }
    }
  }
</style>




