<template>
  <div class="header">
      <div class="header-one">
          <img :src="seller.avatar" alt="">
      </div>
      <div class="header-onen">
          <img :src="seller.avatar" alt="" @click = 'showDetail'>
      </div>
      <div class="header-two">
          <p>{{seller.name}}</p>
          <img  src="../../../static/imgs/brand.png" alt="" >
      </div>
      <div class="write-two">
          <p >{{seller.description}}／{{seller.deliveryTime}}分钟送达</p>
      </div>
      <div class="img2">
           <img  src="../../../static/imgs/decrease.png" alt="">
           <p v-if="seller.supports">{{seller.supports[0].description}}</p>
      </div>
      <div class="header-bottom"></div>
      <div class="header-three"></div>
      <div class="header-four">
            <img src="../../../static/imgs/bulletin.png" alt="">
            <p>{{seller.bulletin}}</p>
            <span>></span> 
      </div>
      <div class="header-right">
          <p v-if="seller.supports">{{seller.supports.length}}个&nbsp;&nbsp;></p>
      </div>
      <div class="clea"></div>
  </div>

</template>

<script>
import { mapGetters , mapActions } from 'vuex';
export default {
  components:{
		},
  computed:{
      
      ...mapGetters([
          'seller'
      ])
  },
  methods:{
			...mapActions([
				'showDetail'
			])
		}
}
</script>

<style scoped lang='less'>
    .header{
        width:100%;
        height: 2.68rem;
        position: absolute;
        .header-one {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: -1;
            filter: blur(5px);
            img{
                width: 100%;
                height: 100%; 
            }
        }
        .header-onen img{
            position: absolute;
            width: 1.28rem;
            height: 1.28rem;
            left: 0.48rem;
            top: 0.48rem;
            border-radius: 0.04rem;
        }
        .header-two{
                img{
                    position: absolute;
                    width: 0.6rem;
                    height: 0.36rem;
                    left: 2.08rem;
                    top: 0.52rem;
            }
                p{
                    position: absolute;
                    color:rgb(255,255,255);
                    font-size: 0.32rem;
                    font-weight: bold;
                    line-height: 0.36rem;
                    left: 2.8rem;
                    top: 0.52rem;
            } 
        }    
        .write-two p{
            position: absolute;
            color:rgb(255,255,255);
            font-size: 0.24rem;
            font-weight: 200;
            line-height: 0.24rem;
            left: 2.08rem;
            top:1.04rem;         
        }
        .img2{
            img{
                position: absolute;
                width: 0.32rem;
                height: 0.32rem;
                left: 2.08rem;
                top: 1.38rem;
            }
            p{
                position: absolute;
                left:2.5rem;
                top: 1.45rem; 
                font-size: 0.2rem;
                color:rgb(255,255,255);
                font-weight: 200;
                line-height: 0.24rem;
            }
        } 
        .header-three{
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: -1;
            height: 2.12rem;
            background-color: rgba(7, 17, 27, 0.5)
        }
         .header-bottom{
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: -1;
            height: 0.56rem;
            background-color: rgba(7, 17, 27, 0.6)
        }
        .header-four {
            img{
                position: absolute;
                width: 0.44rem;
                height: 0.24rem;
                left: 0.24rem;
                top:2.26rem;
            } 
            p{
                position: absolute;
                color:rgb(255,255,255);
                font-size: 0.2rem;
                font-weight: 200;
                line-height: 0.56rem;
                left: 0.76rem;
                top:2.12rem;
                width: 6.5rem;
                height: 0.5rem;
                white-space: nowrap;
                overflow: hidden;
                border:0px solid red;
                text-overflow: ellipsis;
            } 
            span{
                position: absolute;
                color:rgb(255,255,255);
                right: 0.24rem;
                top:2.3rem;
            }
        }
        .header-right{
            position: absolute;
            right: 0.24rem;
            bottom: 0.92rem;
            z-index: -1;
            width: 0.85rem;
            height: 0.48rem;
            border-radius: 20px;
            background-color: rgba(0, 0, 0, 0.3);
        }
        .header-right p{
            font-size: 0.2rem;
            color: rgb(255,255,255);
            font-weight: 200;
            line-height: 0.48rem;
            text-align: center;
            }
            .clea{
                clear: both;
            }
 }
</style>



