<template>
  <div>
      <div class="shopcart">
      <div class="content" @click="toggleList($event)">
        <div class="left">
          <div class="cart"> 
              <div class="logo" :class="{'highlight': totalCount > 0}">
                  <i class="iconfont icon-gouwuche" :class="{'highlight': totalCount > 0}"></i>
              </div>
              <div class="money" :class = "{'cc' : totalCount > 0 }">{{totalCount}}</div>
              <div class="price">￥{{totalPrice}}</div>
              <div class="psf">另需配送费￥4元</div>
           </div>
        </div>
        <div class="right">
            <div class="pay" :class="payClass">
                {{pay}}
            </div>
        </div>
      </div>
      <div class="ball-content">
        
         <div v-for="(ball,b) in balls" :key="b">
             <transition name="drop" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
                <div v-show="ball.show" class="ball">
                    <div class="blue"></div>
                </div>
          </transition>
         </div>
      </div>
      <transition name="fade">
        <div class="shopcart-list" v-show="aaa">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty">清空</span>
          </div>
          <div class="list-content" ref="listContent">
           
            <ul>
              <li class="shopcart-food" v-for="(food,d) in selcetFoods" :key="d">
                <span class="name">{{food.name}}</span>
                <div class="price"><span>￥{{food.price * food.count}}</span></div>
                <div class="cartControl-wrapper">
                  <cartControl :food="food"></cartControl>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
      <transition name="fade">
        <div class="list-mask" v-show="listShow" @click="hideList()"></div>
     </transition>
  </div>
  </div>
</template>


<script>
import { mapGetters } from "vuex";
import BScroll from "better-scroll";
import cartControl from "../cartControl/cartControl.vue";

export default {
  props: ["goods"],
 
  data() {
    return {
      balls: [],
      dropBalls: [],
      fold: true,
      aaa:false
    };
  },
  computed: {
    ...mapGetters(["seller"]),
    // 筛选所有由count的商品
    selcetFoods() {
      let foods = [];
      this.goods.forEach(good => {
        good.foods.forEach(food => {
          if (food.count > 0) {
            foods.push(food);
          }
        });
      });
      return foods;
    },
    // 计算购物车商品的总数量
    totalCount() {
      let count = 0;
      this.selcetFoods.forEach(food => {
        count += food.count;
      });
      return count;
    },
    //计算购物车商品总价
    totalPrice() {
      let price = 0;
      this.selcetFoods.forEach(food => {
        price += food.count * food.price;
      });
      return price;
    },
    //结算描述
    pay() {
      if (this.totalPrice === 0) {
        return `¥ ${this.seller.minPrice} 元起送`;
      } else if (this.totalPrice < this.seller.minPrice) {
        return `还差¥ ${this.seller.minPrice - this.totalPrice} 元起送`;
      } else {
        return "去结算";
      }
    },
    payClass() {
      if (this.totalPrice < 20) {
        return "not-enough";
      } else {
        return "enough";
      }
    },
    listShow() {
      if (!this.totalCount) {
        this.fold = true;
        return false;
      }
      let show = !this.fold;
      if (show) {
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.listContent, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      }
      return show;
    }
  },
  methods: {
    toggleList() {
     this.aaa=!this.aaa;
    },
    empty() {
      // console.log(this.selcetFoods)
      this.selcetFoods.forEach(food => {
        food.count = 0;
      })
    },
    hideList() {
      this.fold = false;
    },
    drop(el) {
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (!ball.show) {
          ball.show = true;
          ball.el = el;
          this.dropBalls.push(ball);
          return;
        }
      }
    }
   
  },
  components: {
    cartControl
  }
};
</script>

<style lang="less" scoped>
    .shopcart{
        width: 100%;
        height: 0.9rem;
        position: fixed;
        left: 0;
        bottom: 0;
        .content{
            display: flex;
            background:#131d26;
            .left{
                flex: 1;
                .cart{
                    width: 1.16rem;
                    height: 1.16rem;
                    display: inline-block;
                    border-radius: 50%;
                    position: relative;
                    top: -0.24rem;
                    margin:0 0.24rem;
                    vertical-align: top;
                    box-sizing: border-box;
                    //   background:rgba(7, 17, 27, 0.8);
                    background: #131d26;
                    .logo{
                        width: 0.92rem;
                        height: 0.92rem;
                        text-align: center;
                        border-radius: 50%;
                        background: #2b343d;
                        position: absolute;
                        top:0.12rem;
                        left: 0.12rem;
                        &.highlight{
                            background: rgb(0, 160, 220);
                        }
                        i{
                            color: #808589;
                            font-size: 0.48rem;
                            position: absolute;
                            top:0.18rem;
                            left: 0.22rem;
                            &.highlight{
                                color: #fff;
                            }
                        }
                    }
                    .money{
                        position: absolute;
                        top: 0;
                        right: 0;
                        width: 24px;
                        height: 24px;
                        line-height: 24px;
                        text-align: center;
                        border-radius: 16px;
                        font-size:9px;
                        font-weight: 700;
                        color: #ffffff;
                        background: rgb(240, 20, 20);
                        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
                    }
                    .price{
                        width:0.8rem ;
                        font-size: 0.32rem;
                        color: #808589;
                        font-weight: 800;
                        line-height: 0.48rem;
                        position: absolute;
                        top: 0.48rem;
                        left: 1.3rem;
                        text-align: center; 
                        border-right: 0.01rem solid #808589; 
                        padding-right: 0.24rem;              
                    }
                    .psf{
                        width: 2rem;
                        height: 0.48rem;
                        font-size: 0.24rem;
                        color: #808589;
                        font-weight: 700;
                        line-height: 0.48rem;
                        padding-top: 0.5rem;
                        padding-left:2.7rem; 
                    }           
                }          
            }
            .right{
                background: #2b343b;
                flex: 0 0 2.1rem;
                width: 2.1rem;
                height: 1rem;
                line-height: 1rem;
                .pay{
                    text-align: center;
                    font-size: 0.24rem;
                    font-weight: 700;
                    color: #9a9ea1;
                    &.enough{
                        background: rgb(0, 160, 220);
                        color: #ffffff;
                    }
                }
            }   
        }
        .ball-content{
            .ball{
                position: fixed;
                left: 0.64rem;
                bottom: 0.44rem;
                z-index :200;
                transition: all 0.6s cubic-bezier(0.49, -0.29, 0.75, 0.41);
                .blue{
                    width: 0.32rem;
                    height: 0.32rem;
                    border-radius: 50%;
                    background: rgb(0, 160, 220);
                    transition: all 0.4s linear;
                }
            }
        }
        .shopcart-list{
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            transform: translate3d(0, -100%, 0);
            &.fade-enter-active, &.fade-leave-active {
                transition: all 0.5s;
                transform: translate3d(0, -100%, 0);
            }
            &.fade-enter, &.fade-leave-active {
                transform: translate3d(0, 0, 0);
            }
            .list-header{
                height: 0.8rem;
                line-height: 0.8rem;
                padding: 0 0.36rem;
                background: #f3f5f7;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                .title{
                    float: left;
                    font-size: 0.28rem;
                    color: rgb(7, 17, 27);
                }
                .empty{
                    float: right;
                    font-size: 0.24rem;
                    color: rgb(0, 160, 220);
                }
            } 
            .list-content{
                padding: 0 0.36rem;
                max-height: 5.16rem;
                background: white;
                overflow: hidden;
                .shopcart-food{
                    position: relative;
                    padding: 0.12rem 0;
                    box-sizing: border-box;
                    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                    .name{
                        line-height: 0.48rem;
                        font-size: 0.28rem;
                        color: rgb(7, 17, 27);
                    }
                    .price{
                        position: absolute;
                        right: 0.18rem;
                        bottom: 0.12rem;
                        font-size: 0.28rem;
                        font-weight: 700;
                        color: rgb(240, 20, 20);
                    }
                    .cartControl-wrapper{
                        position: absolute;
                        right: 0;
                        bottom: 0.12rem;
                    }
                }
            }
        }
        .list-mask{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 40;
            // backdrop-filter: blur(0.2rem);
            opacity: 1;
            background: rgba(7, 17, 27, 0.6);
            &.fade-enter-active, &.fade-leave-active {
            opacity: 1;
            transition: all 0.5s;
            background :rgba(7, 17, 27, 0.6);
            }
            &.fade-enter, &.fade-leave-active {
            opacity: 0;
            background: rgba(7, 17, 27, 0);
            }             
        }
    }
</style>



